.projects-list-wrapper {
  a {
    color: black;
    text-decoration: none;
  }
}

.project-card {
  height: 200px;
  display: flex;
  margin: 32px 0;
  background-color: $flax;
  border-radius: 4px;
  box-shadow: 3px 3px 4px gray;

  img {
    height: 200px;
    width: 445px;
    object-fit: cover;
    margin-right: 18px;
  }
}

.project-card-info {
  padding-right: 16px;
}

@media only screen and (max-width: 1000px) {
  .project-card {
    flex-direction: column;
    height: fit-content;

    img {
      width: 100%;
      height: auto;
    }

    h2 {
      text-align: center;
    }
  }

  .project-card-info {
    padding-left: 16px;
  }
}
