.info-and-picture {
  display: flex;
  justify-content: space-between;

  p, li {
    text-justify: none;
  }
}

.project-information {
  flex-grow: 1;
  flex-shrink: 1;
  margin-right: 24px;
}

.project-picture {
  display: block;
  height: 300px;
  object-fit: cover;
  margin-left: 24px;
  flex-grow: 1;
  flex-shrink: 1;
  align-self: center;
}

@media only screen and (max-width: 1800px) {
  .info-and-picture {
    flex-direction: column;

    p, li {
      text-justify: auto;
    }
  }

  .project-picture {
    width: 75%;
    height: auto;
    margin-left: 0;
    flex-grow: 0;
    flex-shrink: 0;
  }
}

@media only screen and (max-width: 1280px) {
  .project-picture {
    width: 100%;
  }
}
